import React from "react";
import '../styles.scss'
import logo from "../../../img/logo.svg";
import {Link} from "react-scroll";

function sideDrawer(props) {
    let drawerClasses = 'sideDrawer';
    if (props.show) {
        drawerClasses = 'sideDrawer open'
    }

    return (
        <nav className={drawerClasses}>
            <div className='logoContainer'>
                <img src={logo} alt="logo"/>
            </div>
            <hr style={{margin: '0', marginLeft: '40px'}}/>
            <ul>
                <li>
                    <Link
                        to='section2'
                        activeClass="active"
                        spy={true}
                        smooth={true}
                        offset={-100}
                        duration={500}
                        onClick={props.closeAfterClick}
                    >About me</Link>
                </li>
                <li>
                    <Link to=''
                          activeClass="active"
                          spy={true}
                          smooth={true}
                          offset={0}
                          duration={500}
                          onClick={props.closeAfterClick}
                    >
                        Relationship
                    </Link>
                </li>
                <li>
                    <Link
                        to='section1'
                        activeClass="active"
                        spy={true}
                        smooth={true}
                        offset={-80}
                        duration={500}
                        onClick={props.closeAfterClick}
                    >

                        Requirements
                    </Link>
                </li>
                <li>
                    <Link
                        to='section3'
                        activeClass="active"
                        spy={true}
                        smooth={true}
                        offset={-50}
                        duration={500}
                        onClick={props.closeAfterClick}
                    >

                        Users
                    </Link>
                </li>
                <li>
                    <Link to='section4'
                          activeClass="active"
                          spy={true}
                          smooth={true}
                          offset={50}
                          duration={500}
                          onClick={props.closeAfterClick}
                    >

                        Sign Up
                    </Link>
                </li>
                <li>
                    <Link to=''>Terms and Conditions</Link>
                </li>

                <hr/>
                <br/>

                <li>
                    <Link to=''>How it works</Link>
                </li>
                <li>
                    <Link to=''>Partnership</Link>
                </li>
                <li>
                    <Link to=''>Help</Link>
                </li>
                <li>
                    <Link to=''>Leave Testimonial</Link>
                </li>
                <li>
                    <Link to=''>Contact us</Link>
                </li>


                <hr/>
                <br/>


                <li>
                    <Link to=''>Articles</Link>
                </li>
                <li>
                    <Link to=''>Our news</Link>
                </li>
                <li>
                    <Link to=''>Testimonials</Link>
                </li>
                <li>
                    <Link to=''>Licenses</Link>
                </li>
                <li>
                    <Link to=''>Privacy Policy</Link>
                </li>

            </ul>
        </nav>
    )
}

export default sideDrawer
